 * @Description: In User Settings Edit
 * @FilePath: \vue_shop\src\components\users.vue
-->
<template>
    <div>
        <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item><a href="/">活动管理</a></el-breadcrumb-item>
        <el-breadcrumb-item>活动列表</el-breadcrumb-item>
        <el-breadcrumb-item>活动详情</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <el-row :gutter="20">
                <el-col :span="9">
                    <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
                        <el-button slot="append" icon="el-icon-search" @click="getUserList" ></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                         <el-button type="primary" @click="addUser">添加用户</el-button>
                </el-col>
            </el-row>
            <el-table :data="userList" border style="margin-top:20px" stripe>
                <el-table-column  type="index" ></el-table-column>
                <el-table-column label="姓名" prop="username" ></el-table-column>
                <el-table-column label="邮件" prop="email" ></el-table-column>
                <el-table-column label="电话" prop="mobile" ></el-table-column>
                <el-table-column label="角色" prop="role_name" ></el-table-column>
                <el-table-column label="状态" prop="mg_state">
                    <template slot-scope="scope"> 
                        <el-switch
                            v-model="scope.row.mg_state" @change="switChange(scope.row)">
                        </el-switch>
                    </template>
                </el-table-column>
               <el-table-column label="操作">
                    <template slot-scope="scope">
                         <el-tooltip effect="dark" content="修改" placement="top-start" >
                          <el-button type="primary" icon="el-icon-edit"  size="mini"  @click="editDiallog(scope.row.id)"></el-button>
                        </el-tooltip>
                            <el-tooltip effect="dark" content="删除" placement="top-start">
                        <el-button type="danger" icon="el-icon-delete" @click="del(scope.row.id)" size="mini"></el-button>
                         </el-tooltip>
                           <el-tooltip  effect="dark" content="分配角色" placement="top-start">
                            <el-button type="warning" icon="el-icon-share" size="mini" @click="fpjs(scope.row)"></el-button>
                         </el-tooltip>
                    </template>
               </el-table-column>
            </el-table>
                <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[1,2,3,4]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
                </el-pagination>
           
        </el-card>
        <!-- 添加用户新增对话框 -->
        <el-dialog
            title="提示"
            :visible.sync="dialogVisible"
            @close="addDialogCloseds"
            width="30%">
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
                <el-form-item label="用户名" prop="username" >
                    <el-input v-model="addForm.username" />
                </el-form-item>
                 <el-form-item label="密码" prop="password">
                    <el-input v-model="addForm.password" />
                </el-form-item>
                 <el-form-item label="邮箱" prop="email">
                    <el-input v-model="addForm.email" />
                </el-form-item>
                 <el-form-item label="手机" prop="mobile">
                    <el-input v-model="addForm.mobile" />
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="addUsers"> 确 定</el-button>
            </span>
        </el-dialog>
        <!-- 修改对话框 -->
          <el-dialog
            title="修改"
            :visible.sync="editDialog"
            @close="editDialogCloseds"
            width="30%">
            <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
                <el-form-item label="用户名">
                    <el-input v-model="editForm.username" :disabled="true"/>
                </el-form-item>
                 <el-form-item label="邮箱" prop="email">
                    <el-input v-model="editForm.email" />
                </el-form-item>
                 <el-form-item label="手机"  prop="mobile">
                    <el-input v-model="editForm.mobile" />
                </el-form-item>
            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editFormDia"> 确 定</el-button>
            </span>
        </el-dialog>
        <!-- 分配角色对话框 -->
          <el-dialog
            title="分配角色"
            :visible.sync="fpjsDialog"
            @close="fpjsClose"
            width="30%">
            <div>
                <p>当前用户:{{userinfo.username}}</p>
                <p>当前角色：{{userinfo.role_name}}</p>
                 <p>分配新角色：
                     <el-select v-model="value" placeholder="请选择">
                        <el-option
                        v-for="item in jsList"
                        :key="item.id"
                        :label="item.roleName"
                        :value="item.id">
                        </el-option>
                    </el-select>
                 </p>
            </div>
            <span slot="footer" class="dialog-footer" >
                <el-button @click="fpjsDialog = false">取 消</el-button>
                <el-button type="primary" @click="jsfpbtn()"> 确 定</el-button>
            </span>
          </el-dialog>
    </div>
</template>
<script>
import { MessageBox } from 'element-ui'
export default {
    data() {
        var checkEmial = (rule,value,cb) => {
            const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
            if(regEmail.test(value)){
                return cb()
            }
            cb(new Error('请输入合法的邮箱'))
        }
        var checkMobile = (rule,value,cb) => {
            const regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
            if(regMobile.test(value)){
                return cb()
            }
            cb(new Error('请输入合法的手机号'))
        }
        return {
            value:'',
            jsList:[],
            userinfo:{},
            fpjsDialog:false,
            editForm:{},
           queryInfo:{
               query:'',
               pagesize:1,
               pagenum:1,
            },
            editDialog:false,
            userList:[],
            total:0,
            dialogVisible:false,
            addForm:{
                username:'',
                password:'',
                email:'',
                mobile:'',
            },
            addFormRules:{
                username:[
                    {required:true,message:'请输入用户名',trigger:'blur'},
                    {min:3,max:10,message:'用户名的长度在3-10之间',trigger:'blur'}
                ],
                 password:[
                    {required:true,message:'请输入密码',trigger:'blur'},
                    {min:6,max:15,message:'密码的长度在3-15之间',trigger:'blur'}
                ],
                 email:[
                    {required:true,message:'请输入邮箱',trigger:'blur'},
                    {validator:checkEmial,trigger:'blur'}
                ],
                mobile:[
                    {required:true,message:'请输入手机号',trigger:'blur'},
                     {validator:checkMobile,trigger:'blur'}
                ],
            },
            editFormRules:{
                 email:[
                    {required:true,message:'请输入邮箱',trigger:'blur'},
                    {validator:checkEmial,trigger:'blur'}
                ],
                mobile:[
                    {required:true,message:'请输入手机号',trigger:'blur'},
                    {validator:checkMobile,trigger:'blur'}
                ],
            }
        };
    },
    created(){
        this.getUserList()
    },
    mounted() {
        
    },
    methods: {
    async  getUserList(){
         const {data:res} = await this.$http.get('users',{params:this.queryInfo});
         if(res.meta.status != 200) return this.$message.error(res.meta.msg)
        this.userList = res.data.users;
        this.total = res.data.total;
      },
      handleSizeChange(newSize){
         this.queryInfo.pagesize = newSize;
         this.getUserList()
      },
      handleCurrentChange(newPage){
        console.log(newPage)
         this.queryInfo.pagenum = newPage;
         this.getUserList()
      },
    async switChange(scope){
        const {data:res} =  await this.$http.put(`users/${scope.id}/state/${scope.mg_state}`)
        if(res.meta.status != 200) {
         scope.mg_state = !scope.mg_state;
         this.$message.error('更新状态失败')
        } 
        this.$message.success('更新状态成功')
      },
        addUser(){
        this.dialogVisible = true
    },
    addDialogCloseds(){
        this.$refs.addFormRef.resetFields();
    },
    addUsers(){
        this.$refs.addFormRef.validate( async value => {
          if(!value) return
          const {data:res} =  await this.$http.post('users',this.addForm);
          if(res.meta.status != 201) return  this.$message.error('新增用户失败')
          this.$message.success('新增用户成功')
          this.dialogVisible = false
          this.getUserList()
        })
    },
    // 修改
  async  editDiallog(id){
        this.editDialog = true
       const {data:res} = await this.$http.get('users/'+ id);
         if(res.meta.status != 200) return  this.$message.error('获取用户数据失败')
         this.editForm = res.data;
    },
    editDialogCloseds(){
         this.$refs.editFormRef.resetFields();
    },
    editFormDia(){
        this.$refs.editFormRef.validate( async value => {
        if(!value) return
        const {data:res} = await this.$http.put('users/'+ this.editForm.id ,{email:this.editForm.email,mobile:this.editForm.mobile} );
         if(res.meta.status != 200) return  this.$message.error('更新用户数据失败')
         this.editDialog = false;
         this.getUserList()
          this.$message.success('更新用户数据成功')
    })
    },
    // 删除
  async  del(id){
    const ress  =  await MessageBox.confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).catch(err => err)

        if(ress !== 'confirm'){
            return this.$message.info('已取消删除')
        }
        const {data:res} = await this.$http.delete('users/' + id)
        if(res.meta.status != 200) return this.$message.error('删除用户失败')
        this.$message.success('删除用户成功')
        this.queryInfo={
               query:'',
               pagesize:1,
               pagenum:1,
            }
        this.getUserList()
    },
    // 分配角色
  async  fpjs(userinfo){
        this.userinfo = userinfo;
        this.fpjsDialog = true;
         const {data:res} = await this.$http.get('roles/');
         if(res.meta.status != 200) return  this.$message.error('获取用户数据失败')
         this.jsList = res.data;
    },
    async jsfpbtn(){
        const {data:res} = await this.$http.put(`users/${this.userinfo.id}/role`,{rid:this.value})
        if(res.meta.status != 200) return  this.$message.error('更新角色失败')
        this.$message.success('更新角色成功')
         this.getUserList()
         this.fpjsDialog = false
    },
    fpjsClose(){
        this.value = ''
        this.userinfo = {}
    }
    },
};
</script>
<style lang="less" scoped>
    .el-breadcrumb{
        margin-bottom: 20px;
    }
</style>